.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 36px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
	}
	.couple-body {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple-content {
		position: relative;
		background-color: var(--color-primary);
		padding: 3rem;
		border-radius: 300px 300px 20px 20px;
		margin-bottom: 3rem;
		box-shadow: 0 1rem 1.5rem rgba($color: #000000, $alpha: 0.1);
		@include max-md {
			padding: 2rem;
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			height: auto;
			aspect-ratio: 1;
			margin-bottom: 2rem;
			.couple-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px;
			}
		}
	}
	.couple {
		position: relative;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			border-radius: 300px 300px 20px 20px;
			opacity: 0.5;
			content: "";
		}
		.image-wrap {
			width: 100%;
			height: auto;
			position: relative;
			aspect-ratio: 1;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px;
				}
				.frame {
					position: absolute;
					top: 50%;
					left: 50%;
					width: 125%;
					transform: translate(-50%, -50%) rotateZ(25deg);
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 3rem 1rem 1rem;
			h3 {
				font-size: 42px;
				@include max-md {
					font-size: 36px;
				}
			}
			p {
				margin-bottom: 0;
			}
		}
		.couple-parent {
			position: relative;
			width: 100%;
			margin-bottom: 1rem;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
