.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0 0;
		margin-bottom: -5rem;
		z-index: 5;
		@include max-md {
			padding: 3rem 0 0;
			margin-bottom: -3rem;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 7rem 2rem 2rem;
		background-color: var(--color-primary);
		border-radius: 300px 300px 20px 20px;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
