.reminder-wrap {
	position: relative;
	display: flex;
	justify-content: center;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 125%;
		transform: translate(-50%, -50%);
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		padding: 5rem;
		z-index: 2;
		@include max-md {
			padding: 3rem 5rem;
		}
		@include max-xs {
			padding: 3rem;
			width: 100%;
		}
		.elements-wrapper-absolute {
			.background-element {
				overflow: hidden;
				border-radius: 300px;
			}
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
