.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 1;
		margin-bottom: 2rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px;
			}
			.frame {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 125%;
				transform: translate(-50%, -50%) rotateZ(25deg);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.elements-wrapper-absolute {
			.background-element {
				overflow: hidden;
				border-radius: 300px 300px 20px 20px;
				box-shadow: inset 0 20px 40px rgba($color: #000000, $alpha: 0.2);
				img {
					opacity: 0.4;
				}
			}
		}
		.thank {
			position: relative;
			.thank-body {
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
