.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 3rem 2rem;
				border-radius: 300px 300px 20px 20px;
				width: 40%;
				min-height: 80%;
				@include max-lg {
					width: 60%;
				}
				@include max-md {
					width: 100%;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-primary);
					border-radius: 300px 300px 20px 20px;
					opacity: 0.6;
					content: "";
					box-shadow: inset 0 1.5rem 2.5rem rgba($color: #000000, $alpha: 0.2);
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: center;
					color: var(--color-dark);
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
					.title {
						margin: 1rem 0;
					}
					.cover-event {
						position: relative;
						h6,
						span {
							font-weight: 600;
							margin-bottom: 0;
						}
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
